import { useRouteError } from 'react-router-dom';

function ErrorComponent(props) {
const {error} = props;
  
  console.log(error);

  if (error && error.status === 401) {
    // Customize the error component for status 401
    return (
      <main>
        <div>
          <h1>{error.status}</h1>
          <h2>{error.data.sorry}</h2>
          <p>
            Go ahead and email {error.data.hrEmail} if you feel like this is a
            mistake.
          </p>
        </div>

        <div className="status-bar card error">
          <span>
            "We encountered an issue while loading this page. Please try again,
            and if the problem persists, kindly contact the website
            administrators for assistance."
          </span>
        </div>
      </main>
    );
  }

  // Default error component for other error cases
  return <div>Oops! Something went wrong.</div>;
}

export default ErrorComponent;
